.main-nav {
  position: relative;
  @media (min-width: @tablet-width) {
    min-height: 56px;
    .main-nav__toggle {
      display: none;
    }
  }
}

.main-nav__link {
  display: block;
  padding: 8px 20px 6px 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  color: @color-white;
  background-color: @color-blue;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-top: 1px solid #678fa8;
  border-bottom: 1px solid #9ac2db;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
    color: #88b6d1;
  }

  @media (min-width: @tablet-width) {
    padding: 15px 15px 15px 18px;
    font-size: 14px;
    font-weight: bold;
    line-height: 26px;
    color: @color-black;
    background-color: @color-white;
    box-shadow: none;
    border: none;

    &:hover,
    &:active {
    background-color: transparent;
    }

    &:hover {
      color: @color-blue;
    }

    &:active {
      color: @color-black;
      opacity: 0.3;
    }
  }
}

.main-nav__link--active {
  @media (min-width: @tablet-width) {
    color: @color-dark-orange;

    &:hover {
      color:#604E43;
    }

    &:active {
      color:#766357;
      opacity: 0.3;
    }
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.main-nav__item {
  @media (min-width: @tablet-width) {
    width: 153px;
    &:nth-child(2n + 2) {
      margin-right: 156px;
    }
    &:nth-child(4) {
      margin-right: 0;
    }
  }

  @media (min-width: @desktop-width) {
    width: 239px;

    &:nth-child(2n + 2) {
      margin-right: 243px;
    }

    &:nth-child(4) {
      margin-right: 0;
    }
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  cursor: pointer;
  border: none;
  color: @color-white;
  background-color: transparent;

  &:hover,
  &:active {
    opacity: 0.6;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 27px;
  width: 23px;
  height: 3px;
  background-color: @color-blue;
  box-shadow: 0 8px 0 0 @color-blue, 0 16px 0 0 @color-blue;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(@color-blue, 30%);
  box-shadow:
    0 8px 0 0 fade(@color-blue, 30%),
    0 16px 0 0 fade(@color-blue, 30%);
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__item {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .main-nav__item {
    display: block;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 26px;
    left: 26px;
    width: 25px;
    height: 3px;
    background-color: @color-white;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: fade(#ffffff, 30%);
  }
}
