.advantages__header {
  padding: 33px 28px 31px 25px;

  @media (min-width: @tablet-width) {
    padding: 45px 125px 53px 125px;
  }
  @media (min-width: @desktop-width) {
    padding: 39px 348px 42px 348px;
  }
}

.advantages__header
.advantages__title {
  line-height: 26px;
  padding-bottom: 31px;
}

.advantages__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  margin: 0;
  padding-bottom: 10px;
  @media (min-width: @tablet-width) {
    padding: 2px 40px 14px;
    font-size: 21px;
    line-height: 26px;
  }
  @media (min-width: @desktop-width) {
    padding: 20px 30px 20px;
  }
}

.advantages__wrapper .advantages__description,
.advantages__wrapper .advantages__number {
  color: @color-white;
}

.advantages__number,
.advantages__description {
  color: @color-dark-gray;
  text-align: center;
  margin: 0;
}

.advantages__description {
  line-height: 24px;
  @media (min-width: @tablet-width) {
    padding: 0 5px;
  }
  @media (min-width: @desktop-width) {
    padding: 0 10px;
  }
}

.advantages__header
.advantages__description {
  line-height: 26px;
}

.advantages__list {
  display: grid;
  background-color: @color-light-gray;
  padding: 0;
  margin: 0;
  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(3, 256px);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 400px);
  }
}

.advantages__wrapper {
  color: @color-white;
  background-color: @color-blue;
  grid-column-start: 1;
  grid-column-end: -1;
}

.advantages__item--left,
.advantages__item--right {
  @media (min-width: @tablet-width) {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  height: 256px;
  }
  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item--left .advantages__pic {
  grid-column-start: 2;
  grid-column-end: -1;
}

.advantages__item--right .advantages__text {
  padding: 38px 12px 38px 14px;
  order: 2;
  @media (min-width: @desktop-width) {
    padding: 31px 70px 30px 70px;
  }
}

.advantages__item--right .advantages__pic {
  grid-column-start: 1;
  grid-column-end: 3;
}

.advantages__item--right .advantages__description {
  @media (min-width: @tablet-width) {
    padding: 0 17px;
  }


}

.advantages__pic {
  margin-bottom: -6px;
}

.advantages__item--without-photo {
  padding: 25px 30px;
  position: relative;
  @media (min-width: @tablet-width) {
    padding: 38px 30px 37px;
  }
  @media (min-width: @desktop-width) {
    padding: 23px 31px 52px 38px
  }

}

.advantages__item--without-photo :before {
  @media (max-width: @mobile-width-only) {
  content: "";
  position: absolute;
  top: 2px;
  left: 20px;
  width: 280px;
  border-top: 1px solid #d6d6d6;
  }
}

.advantages__item--without-photo  .advantages__description {
  padding: 5px;
  line-height: 21px;
}

/*.advantages__item--without-photo .advantages__title {
  padding: 20px 94px 20px;
}*/

.advantages__text {
  padding: 52px 10px 36px 14px;
  @media (min-width: @tablet-width) {
    padding: 41px 27px;;
  }
  @media (min-width: @desktop-width) {
    padding: 27px 68px;
  }
}

.advantages__number {
  padding-bottom: 15px;
}
