.impression-form__item {
  margin-bottom: 31px;
}


.impression-form__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  padding-left: 34px;
  padding-top: 14px;
}

.impression-form {
  @media (min-width: @tablet-width) {
    order: -1;
  }
}

.impression-form__checkbox + label::before {
  content: "";
  position: absolute;
  left: -42px;
  top: -1px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-color: #F2F2F2;
}

.impression-form__checkbox:checked+label::after {
  content: "";
  position: absolute;
  top: 17px;
  left: -25px;
  width: 21px;
  height: 21px;
  background-color: #333333;
  border-radius: 50%;
}

.impression-form .form-review__title {
  padding-top: 34px;
  @media (min-width: @tablet-width) {
    padding-top: 0;
  }

}
