.video {
  margin-bottom: 45px;
  @media (min-width: @tablet-width) {
    position: relative;
  }

}

.video__text {
  padding:  46px 25px 33px 31px;
  text-align: center;
  @media (min-width: @tablet-width) {
    padding: 51px 32px 82px 29px;
  }
  @media (min-width: @desktop-width) {
    padding: 62px 25px 82px 29px;
  }

}

.video__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  @media (min-width: @tablet-width) {
    font-size: 30px;
  }
}

.video__description {
  color: @color-dark-gray;
  margin: 0;
  padding-top: 14px;
  line-height: 24px;
  @media (max-width: @mobile-width-only) {
    br {
      display: none;
    }
}
  @media (min-width: @tablet-width) {
    padding-top: 19px;
  }
}

.video__screen {
  @media (min-width: @tablet-width) {
    padding: 0 60px;
    min-width: 648px;
  }
  @media (min-width: @desktop-width) {
    padding: 0 200px;
    min-width: 800px;
  }
}

.video__controls {
  display: flex;
  flex-wrap: wrap;
  margin: 0 20px;
  margin-top: -50px;
  background-color: #f2f2f2;
  @media (min-width: @tablet-width) {
    display: flex;
    align-items: center;
    position: absolute;
    box-sizing: border-box;
    bottom: 4px;
    left: 60px;
    margin: 0;
    padding: 0 15px;
    width: 648px;
    height: 93px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 100;
  }
  @media (min-width: @desktop-width) {
    left: 200px;
    width: 800px;

  }
}

.video__control-slider {
  position: relative;
  padding: 20px 22px;
  padding-top: 22px;
  height: 3px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
  @media (min-width: @tablet-width) {
    order: 2;
    flex-grow: 1;
    padding: 22px 25px 22px 22px;
    width: auto;
    background-color: transparent;
  }
  &--line {
    height: 3px;
    background-color: rgba(225, 225, 255, 0.3);
  }

  &--toggle {
    position: absolute;
    width: 9px;
    height: 9px;
    top: 50%;
    left: 58px;
    margin-top: -3px;
    border-radius: 5px;
    background-color: #ffffff;
    @media (min-width: @tablet-width) {
      left: 136px;
      margin-top: -5px;
    }
    &:focus {
      outline: #81b3d2 auto 5px;
    }
  }
}

.video__control-btn {
  margin: 0;
  padding: 15px 10px;
  box-sizing: content-box;
  width: 36px;
  height: 36px;
  font-size: 0;
  background-color: #f2f2f2;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border: none;
  box-shadow: none;
  outline: none;
  cursor: pointer;
  @media (min-width: @tablet-width) {
    cursor: pointer;
    margin: 0;
    padding: 10px;
    background-color: transparent;
  }

  &--replay {
    margin-left: 8px;
    margin-right: auto;
    @media (min-width: @tablet-width){
      order: 1;
      margin-left: 1px;
    }
    @media (min-width: @desktop-width) {
      margin-left: 3px;
    }
  }

  &--subtitles {
    @media (min-width: @tablet-width){
      order: 3;
      margin-left: 7px;
    }
  }

  &--fullscreen {
    margin-right: 5px;
    @media (min-width: @tablet-width){
      order: 4;
      margin-right: 8px;
    }
  }

  &:focus {
    fill: #333333;
    outline: #5496bd auto 5px;
    @media (min-width: @tablet-width){
      fill: #5c6870;
    }
  }

}

.video__control-btn-icon {
  cursor: pointer;
  fill: #5c6870;
  @media (min-width: @tablet-width){
    cursor: pointer;
    fill: #fff;
  }

  &:hover,
  &:focus {
    fill: #333333;
  }

  &:active {
    fill: #333333;
    opacity: 0.3;
  }
}

.video__control-btn-icon--subtitles {
  padding-top: 8px;

}
