.page-footer__wrapper {
  display: grid;
  text-align: center;
  padding: 32px 48px 15px;
  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
    padding: 54px 0;
  }
  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    padding: 55px 0;
  }
}

.page-footer__hashtag {
  margin: 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
}
