.main-header__title {
  background-color: @color-blue;
  background-image: url(../img/intro-triangle-mobile.svg), url(../img/back-mobile.jpg);
  background-repeat: no-repeat;
  background-position: bottom, 50%;
  background-size: auto, cover;
  min-height: 156px;
  @media (min-resolution: 2dppx) {
    background-image: url(../img/intro-triangle-mobile.svg), url("../img/back-mobile@2x.jpg");
  }
  @media (min-width: @tablet-width) {
    min-height: 290px;
    background-image: url(../img/intro-triangle-tablet.svg), url(../img/back-tablet.jpg);
    @media (min-resolution: 2dppx) {
      background-image: url(../img/intro-triangle-tablet.svg), url("../img/back-tablet@2x.jpg");
    }
  }
  @media (min-width: @desktop-width) {
    min-height: 300px;
    background-image: url(../img/intro-triangle-desktop.svg), url(../img/back-desktop.jpg);
    @media (min-resolution: 2dppx) {
      background-image: url(../img/intro-triangle-desktop.svg), url("../img/back-desktop@2x.jpg");
    }
  }
}

.main-header__title img {
  height: 40px;
  width: 230px;
  padding: 52px 45px;
  @media (min-width: @tablet-width) {
    height: 78px;
    width: 457px;
    padding: 89px 155px;
  }
  @media (min-width: @desktop-width) {
    padding: 103px 371px;
  }
}

.main-header__content {
  text-align: center;
  padding: 21px 18px;
  border-bottom: 1px #E5E5E5 solid;
  @media (min-width: @tablet-width){
    padding: 68px 162px;
    border-bottom: none;
  }
  @media (min-width: @desktop-width) {
    padding: 95px 90px 59px 95px;
  }

}

.main-header__text--medium {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding-top: 18px;
  @media (min-width: @tablet-width) {
    font-size: 30px;
  }
}

.main-header__text--small {
  color: @color-dark-gray;
  line-height: 24px;
  margin: 0;
  padding-top: 27px;
  @media (max-width: @mobile-width-only) {
    br {
      display: none;
    }
  }
  @media (min-width: @desktop-width) {
    padding-top: 33px;
  }
}
