.button {
  display: inline-block;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: @color-white;
  background-color: @color-blue;
  text-transform: uppercase;
  padding: 17px 30px 15px;
  border: none;
  font-style: normal;
}

.button:hover {
  background-color: #669EC0;
}

.button:active {
  background-color: #5496BD;
}

.button:disabled {
  background-color: #E5E5E5;
}
