.item-advantages {
  width: 320px;
  background-color: @color-white;
  @media (min-width: @tablet-width) {
    width: 768px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
  }
}

.item-advantages__list {
  padding: 0;
  display: grid;
  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.item-advantages__title,
.item-advantages__description {
  margin: 0;
  @media (max-width: @mobile-width-only) {
    br {
      display: none;
    }
  }
}

.item-advantages__item {
  position: relative;
  padding: 34px 40px 0 70px;
  @media (min-width: @tablet-width) {
    padding: 158px 32px 60px;
    text-align: center;
    align-self: start;
  }
  @media (min-width: @desktop-width) {
    padding: 157px 54px 80px;
  }
}

.item-advantages__title {
  margin-top: 2px;
  margin-bottom: 15px;
  font-size: 18px;
    @media (min-width: @tablet-width) {
    font-size: 21px;
    margin-bottom: 27px;
  }
}

.item-advantages__item--house::before {
  content: "";
  position: absolute;
  width: 39px;
  height: 37px;
  top: 26px;
  left: 23px;
  background-image: url(../img/icon-house.svg);
  background-repeat: no-repeat;
  background-size: contain;
  @media (min-width: @tablet-width) {
    width: 75px;
    height: 72px;
    top: 61px;
    left: 90px;
  }
  @media (min-width: @desktop-width) {
  top: 61px;
  left: 161px;
  }
}

.item-advantages__item--gift {
  order: 2;
  @media (min-width: @tablet-width) {
    order: 3;
  }
}

.item-advantages__item--gift::before {
  content: "";
  position: absolute;
  width: 32px;
  height: 38px;
  top: 20px;
  left: 26px;
  background-image: url("../img/icon-gift.svg");
  background-repeat: no-repeat;
  background-size: contain;
  @media (min-width: @tablet-width) {
    width: 64px;
    height: 75px;
    top: 57px;
    left: 95px;
  }
  @media (min-width: @desktop-width) {
    top: 57px;
    left: 167px;
  }
}

.item-advantages__item--food {
  order: 3;
  margin-bottom: 32px;
  @media (min-width: @tablet-width) {
    margin-bottom: 0;
    order: 2;
  }
}

.item-advantages__item--food::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 35px;
  top: 22px;
  left: 19px;
  background-image: url("../img/icon-burger.svg");
  background-repeat: no-repeat;
  background-size: contain;
  @media (min-width: @tablet-width) {
    width: 77px;
    height: 68px;
    top: 64px;
    left: 89px;
  }
  @media (min-width: @desktop-width) {
    top: 64px;
    left: 161px;
  }
}
