.contact-form {
  @media (min-width: @tablet-width) {
    grid-column-start: 1;
    grid-column-end: -1;
    padding: 12px 58px 62px 59px;
  }
  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    padding: 59px 133px 38px 66px;
  }
}

.contact-form__field .field__label {
  margin-left: 20px;
}

.contact-form__field {
@media (min-width: @desktop-width) {
  display: flex;
  justify-content: space-between;
}
}
