.copyright {
  padding-top: 40px;
  display: flex;
  justify-content: center;
  @media (min-width: @tablet-width) {
    padding-top: 0;
  }
}

.copyright__link-icon path{
  &:hover{
    fill: @color-blue;
  }

  &:active {
    fill: @color-black;
    opacity: 0.3;
  }
}

.copyright__text {
  padding-left: 12px;
  padding-bottom: 5px;
  align-self: center;
  margin: 0;
}
