.social {
  padding-top: 34px;
  @media (min-width: @tablet-width) {
    padding-top: 0;
  }
}

.social__list {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 152px;
  margin: 0 auto;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background-color: @color-blue;

  &:hover {
    background-color:#669EC0;
  }

  &:active {
    background-color:#5496BD;
  }
}
