.page {
  height: 100%;
  font-size: 14px;
  font-family: "PT Sans", Arial, sans-serif;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  background-color: @color-light-grayish;
}

.page_wrapper {
  max-width: 100%;
  width: 320px;
  overflow: hidden;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  @media (min-width: @tablet-width) {
    max-width: 100%;
    width: 768px;
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }

  @media (min-width: @desktop-width) {
    max-width: 100%;
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: @color-white;
  color: @color-black;
}
