.form-review {
  margin-bottom: 43px;
}

.form-review__title {
  text-align: center;
  padding-top: 15px;
  @media (min-width: @tablet-width) {
    padding-top: 0;
  }
  @media (min-width: @desktop-width) {}
}

.form-review__label,
.form-review__title,
.form-review textarea {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form-review__title,
.form-review__label {
  @media (min-width: @tablet-width) {
    font-size: 21px;
  }
}

.form-review textarea {
  text-transform: none;
  box-sizing: border-box;
  padding: 15px 20px;
  margin-top: 15px;
  width: 100%;
  height: 160px;
  min-width: 150px;
  font-family: "PT Sans","Arial",sans-serif;
  color: #aaa;
  border: 0;
  background-color: #f2f2f2;
  resize: none;
  outline: 0;
  overflow: auto;



  @media (min-width: @tablet-width) {
    margin-top: 55px;
  }

}

.form-review__item {
  border: none;
  padding: 7px 19px;
  border-bottom: 1px #E5E5E5 solid;
  @media (min-width: @tablet-width) {
    padding: 43px 49px;
    border-bottom: none;
  }
  @media (min-width: @desktop-width) {
    padding: 43px 80px 45px;
  }
}

.form-review__item:last-child {
  border-bottom: none;
}


.form-review__wrapper {
  display: grid;
  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.form-review__bottom-wrap {
  text-align: center;
  @media (min-width: @tablet-width) {
    display: grid;
    padding: 0 60px 0px;
    grid-column-start: 1;
    grid-column-end: -1;
    grid-template-columns: 1fr 1fr;
  }
  @media (min-width: @desktop-width) {
    padding: 0 120px;
  }

}

.form-review__button {
  width: 280px;
  margin-top: 28px;
  @media (min-width: @tablet-width) {
    margin-top: 40px;
    width: 323px;
  }
  @media (min-width: @desktop-width) {
    width: 484px;
    margin-top: 33px;
  }
}

.form-review__wrap-message {
  display: none;
  @media (min-width: @tablet-width) {
    display: block;
    align-self: center;
    justify-self: end;
  }
}

