.field__label {
  color: @color-dark-gray;
  line-height: 26px;
  margin-right: 14px;
  align-self: center;
}

.field__input::placeholder {
  box-sizing: border-box;
  color: #aaa;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.field input[type="text"],
.field input[type="tel"],
.field input[type="email"] {
  box-sizing: border-box;
  position: relative;
  padding: 16px 22px;
  width: 100%;
  min-width: 100px;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  color: #000000;
  border: none;
  background-color: #f2f2f2;
  outline: none;

  &:hover {
    background-color: #e5e5e5;
  }

  &:focus {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 4px #e5e5e5;
  }
}


.field {
	input[type="text"] {
		&:focus {
			&::placeholder {
				opacity: 0;
			}
		}
	}
	input[type="tel"] {
		&:focus {
			&::placeholder {
				opacity: 0;
			}
		}
	}
	input[type="email"] {
		&:focus {
			&::placeholder {
				opacity: 0;
			}
		}
	}
}



