.hotel-search {
  text-align: center;
  padding: 32px 17px 50px;
  @media (min-width: @tablet-width) {
    padding: 58px 150px 60px;
  }
  @media (min-width: @desktop-width) {
    padding: 58px 150px 65px;
  }
}

.hotel-search__title {
  margin: 0;
  padding-bottom: 20px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  @media (min-width: @tablet-width) {
    font-size: 30px;
    padding-bottom: 35px;
  }
  @media (min-width: @desktop-width) {
    padding-bottom: 32px;
  }

}

.hotel-search__description {
  line-height: 24px;
  margin: 0;
  padding-bottom: 29px;
  @media (max-width: @mobile-width-only) {
    br {
      display: none;
    }
  }
  @media (min-width: @tablet-width) {
    padding-bottom: 36px;
  }
  @media (min-width: @desktop-width) {
    padding-bottom: 45px;
  }
}

.hotel-search .hotel-search__button {
  padding: 15px 47px 17px;
  margin-left: 3px;
  @media (min-width: @tablet-width) {
    min-width: 365px;
  }

}
