.figcaption-photo__likes-number,
.figcaption-photo__title {
  font-size: 14px;
  font-weight: 700;
}

.figcaption-photo__title {
  margin: 0;
  padding-top: 12px;
}

.figcaption-photo__text {
  color: @color-dark-gray;
  text-transform: none;
  margin: 0;
  padding-top: 4px;
}

.figcaption-photo__likes-img {
  padding-right: 10px;
  opacity: 0.3;
  @media (min-width: @desktop-width) {
    fill: @color-white;
    opacity: 1;
  }
}

.figcaption-photo__likes-wrapper {
  align-self: center;
  padding-top: 10px;
}

.figcaption-photo__likes-number,
.figcaption-photo__title,
.figcaption-photo__text {
  @media (min-width: @desktop-width) {
    color: @color-white;
  }
}
