.attractions-form__item {
  margin-bottom: 25px;
}

.attractions-form__list {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 14px -79px 5px 15px;
    gap: 9px;
  }
  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}

.attractions-form__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
  padding: 14px 0 14px 37px;
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  @media (min-width: @tablet-width) {
    padding: 14px 0 14px 37px;
  }
}

.attractions-form {
  grid-column-start: 1;
  grid-column-end: -1;
  @media (min-width: @tablet-width) {
    padding: 30px 140px;
  }
  @media (min-width: @desktop-width) {
    padding: 29px 193px 20px 109px
  }
}

.attractions-form__checkbox + label::before {
  content: "";
  position: absolute;
  top: 0;
  left: -42px;
  width: 56px;
  height: 56px;
  border-radius: 5px;
  background-color: #f2f2f2;
}

.attractions-form__checkbox:checked+label::after {
  content: url(../img/tick.svg);
  position: absolute;
  top: 16px;
  left: -25px;
  width: 25px;
  height: 19px;
}
