.photo img {
  display: block;
  margin: 0;
}

.photo__wrapper {
  @media (min-width: @tablet-width) {
    padding: 0px 60px;
  }
  @media (min-width: @desktop-width) {
    padding: 0;
    margin-top: 15px;
  }
}

.photo__list {
  display: grid;
  padding: 0;
  margin: 0;
  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.photo__item--large {
  grid-column-start: 1;
  grid-column-end: -1;
}

.photo__figcaption {
  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    padding: 8px 16px 19px;
  }
  @media (min-width: @desktop-width) {
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,.3);

  }
}

.photo__figure {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 29px 19px 10px 20px;
  @media (min-width: @tablet-width) {
    padding: 0px;
    background-color: #F2F2F2;
    margin-top: -5px;
  }
  @media (min-width: @desktop-width) {
    position: relative;
  }
}

.added {
  fill-opacity: #000000;
  opacity: 1;
}
